@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
  display: flex;
  justify-content: center;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0)), url('https://i.pinimg.com/originals/6c/73/41/6c7341f8ceef532c3ecbb7420b6adeb3.jpg');
  background-size: 1500px auto;
  background-attachment: fixed;
}

body {
  box-shadow: 0px 0px 30px 8px rgba(0,0,0,0.75);;
  width: 90%;
  max-width: 650px;
  color: white;
}

.App {
  height: 100%;
  text-align: center;
  font-family: 'Quicksand', sans-serif;
}

h1 {
  font-weight: 550;
  letter-spacing: 0.1em;
}

p {
  font-size: 0.9em;
}

.grid-area {
  margin: 2.5em;
  display: flex;
  justify-content: center;
  height: 65vw;
  max-height: 440px;
  min-height: 300px;
}

.button-bar {
  margin-top: 50px;
}

.button-bar button {
  width: 9em;
  margin: 1em;
  padding: 0.8em 1em;
  border-radius: 0.4em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Quicksand', sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  color: #FFFFFF;
  box-shadow:inset 0 -0.6em 0 -0.35em rgba(0,0,0,0.17);
  text-align:center;
  position: relative;
  cursor: pointer;
}

#random-button {
  background-color:#668fff;
}

#reset-button {
 background-color: #eb675e;
}

#solve-button {
  background-color: #a5cc52;
}

button:active {
  top:0.1em;
}

footer {
  margin-top: 5em;
  
}

* {
  font-family: 'Quicksand', sans-serif;
}

*:focus {
  outline: 0;
}
.Square {
    cursor: pointer;
    font-size: 1.15em;
    font-weight: 400;
    background-color: rgba(255, 255, 255, 0.762);
}

.sideFocus {
    background-color: rgba(225, 235, 249, 0.893);
}

.inFocus {
    background-color: rgba(197, 215, 252, 0.954);
}

.inConflict {
    color: rgb(252, 55, 64);
}

.top {
    border-top: 0.3em solid gray;
}

.bottom {
    border-bottom: 0.3em solid gray;
}

.left {
    border-left: 0.3em solid gray;
}

.right {
    border-right: 0.3em solid gray;
}

.bottom-light {
    border-bottom: 0.2em solid gray;
}

.right-light {
    border-right: 0.2em solid gray;
}
.grid {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(9, 1fr);
    grid-column-gap: 0px;
    width: 80%;
    max-width: 450px;
    min-width: 300px;
    max-height: 450px;
    min-height: 300px;
}
